* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Open Sans', sans-serif;
}


.container {
    max-width: 1280px;
    margin: 0 auto;
    padding: 0 2%;
}

.clear {
    clear: both;
}

body {
    background: #252525;
}

header {
    padding: 3em;
    background-color: #FFCB00;
    box-shadow: 0 8px 32px 0 rgb(21, 21, 21);
    margin-bottom: 5em;
}


.logo {
  width: 75px; /* Ajuste o valor da largura conforme necessário */
  height: auto; /* Mantém a proporção da imagem */
  position: absolute;
  top: 10px;
  left: 20px;
}


.input1 {
