}

.input1 span, i{
 font-style: italic;
 font-weight: normal;
 text-align: center;
 font-size: 14px;
 color: #F5F5F5;
}

.botao1 {
    position: relative;
    background: #0A3871;
    color: white;
    width: 200px;
    margin: 0.5rem;
    padding: 0.7rem;
    border-radius: 20px;
    margin-top: 20px;
    cursor: pointer;
    border: none;
    -webkit-border-radius: 20px;
    -moz-border-radius: 20px;
    -ms-border-radius: 20px;
    -o-border-radius: 20px;
    margin-bottom: 5em;
}

.botao2 {
    position: relative;
    background: #D8DFE8;
    color: #0A3871;
    width: 200px;
    margin: 0.5rem;
    padding: 0.7rem;
    border-radius: 20px;
    margin-top: 20px;
    cursor: pointer;
    border: none;
    -webkit-border-radius: 20px;
    -moz-border-radius: 20px;
    -ms-border-radius: 20px;
    -o-border-radius: 20px;
    margin-bottom: 5em;
}

span {
    font-size: 14px;
}

.box {

    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    max-width: 30em;
    float: right;
    height: 35em;
    background: #343A40;
    box-shadow: 0 8px 32px 0 rgb(21, 21, 21);
    backdrop-filter: blur(5.5px);
    -webkit-backdrop-filter: blur(5.5px);
    border-radius: 10px;

}

.box h3 {
    width: 16rem;
    text-align: center;
    color: #E0E0E0;
    margin-bottom: 5em;
}

.box p {
    font-size: 14px;
    text-align: center;
    color: #E0E0E0;
}

.botao3 {
    display: none;
    background: #FFFFFF;
    color: #0A3871;
    width: 200px;
    margin: 0.5rem;
    padding: 0.7rem;
    border-radius: 20px;
    margin-top: 20px;
    cursor: pointer;
    border: 1px solid #0A3871;
    -webkit-border-radius: 20px;
    -moz-border-radius: 20px;
    -ms-border-radius: 20px;
    -o-border-radius: 20px;


}

.box textarea{
    padding: 2rem;
    color: #E0E0E0;
    font-size: 32px;
    resize: none;
    border: none;
    width: 30em;
    height: 100%;
    background: #343A40;
    outline: 0;
    display: none;
    margin-bottom: 28px;
}

body, html {
  height: 100%;
}

#root, .app-container {
  min-height: 100%;
  display: flex;
  flex-direction: column;
}

.footer {
  background-color: #FFCB00;
  padding: 1rem;
  text-align: center;
  margin-top: auto;
}


#####
html,
body {
    margin: 0;
    padding: 0;
    width: 100%;
    height: 100vh;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    background: #000;
}

.glow-on-hover {
    width: 220px;
    height: 50px;
    border: none;
    outline: none;
    color: #fff;
    background: #111;
    cursor: pointer;
    position: relative;
    z-index: 0;
    border-radius: 10px;
}

.glow-on-hover:before {
    content: '';
    background: linear-gradient(45deg, #ff0000, #ff7300, #fffb00, #48ff00, #00ffd5, #002bff, #7a00ff, #ff00c8, #ff0000);
    position: absolute;
    top: -2px;
    left:-2px;
    background-size: 400%;
    z-index: -1;
    filter: blur(5px);
    width: calc(100% + 4px);
    height: calc(100% + 4px);
    animation: glowing 20s linear infinite;
    opacity: 0;
    transition: opacity .3s ease-in-out;
    border-radius: 10px;
}

.glow-on-hover:active {
    color: #000
}

.glow-on-hover:active:after {
    background: transparent;
}

.glow-on-hover:hover:before {
    opacity: 1;
}

.glow-on-hover:after {
    z-index: -1;
    content: '';
    position: absolute;
    width: 100%;
    height: 100%;
    background: #444444;
    left: 0;
    top: 0;
    border-radius: 10px;
}

@keyframes glowing {
    0% { background-position: 0 0; }
    50% { background-position: 400% 0; }
    100% { background-position: 0 0; }
}
