@media screen and (max-width:768px) {
    .inputs{
        display: flex;
    }
    .input1{
        float: none;
    }

    .box{
       float: none;
       height: 15em;
       margin: 30px;

    }
    .box img{
        display: none;
    }

}

@media screen and (max-width:425px) {

    .input1 textarea{
        width: 100%;
    }

    .box textarea{
        width: 100%;
        height: 100%;
        font-size: 16px;
    }

    .botoes{
        display: flex;
        flex-direction: column;
        align-items: center;
        width: 100%;
        flex-wrap: wrap;

    }

}

